.promo {
  background-image: url(../img/BG.jpg);
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  position: relative;
  padding: 45px 0;
  box-sizing: border-box;
  &__wrapper {
    position: relative;
    @include respond-to(543) {
      flex-direction: row; }
    &:after {
      content: "";
      width: 192px;
      height: 120px;
      background: url(../img/contur.png) no-repeat center center;
      position: absolute;
      bottom: -130px;
      right: 20px;
      @include respond-to(480) {
        display: none; } } }
  &__title {
    height: 116px;
    width: 254px;
    background: $white;
    position: relative;
    display: inline-block;
    @include respond-to(480) {
      width: 35vw;
      height: max-content;
      padding: 10px; }
    h2 {
      font-size: 70px;
      display: inline-block;
      height: 116px;
      line-height: 116px;
      @include respond-to(480) {
        font-size: 8vw;
        line-height: normal;
        height: auto; } }
    &:before {
      content: "";
      height: 116px;
      width: 9999px;
      position: absolute;
      right: 254px;
      top: 0;
      background: $white;
      @include respond-to(480) {
        right: 39vw;
        height: 100%; } } }
  &__full-kit {
    display: inline-block;
    color: $white;
    font-family: $museoBold;
    @include respond-to(543) {
      margin-top: 20px; }
    @include respond-to(480) {
      margin-top: 0px; }
    p {
      font-size: 22px;
      display: block;
      @include respond-to(480) {
        font-size: 4vw; } }
    strong {
      font-size: 64px;
      display: inline-block;
      margin: 0;
      padding: 0;
      @include respond-to(605) {
        font-size: 40px; }
      @include respond-to(480) {
        font-size: 7vw; } }
    span {
      font-size: 22px;
      display: inline-block;
      margin-left: 10px;
      @include respond-to(480) {
        font-size: 4vw; } } }
  &__code {
    display: inline-block;
    width: 228px;
    height: 116px;
    border-radius: 5px;
    border: 2px dashed $white;
    background-color: $green3;
    color: $white;
    font-family: $museoBold;
    box-sizing: border-box;
    padding: 9px 20px;
    @include respond-to(819) {
      margin-top: 20px; }
    @include respond-to(480) {
      width: 41vw;
      height: 100%; }
    p {
      font-size: 20px;
      display: block;
      @include respond-to(480) {
        font-size: 5vw; } }
    strong {
      font-size: 58px;
      display: block;
      @include respond-to(480) {
        font-size: 8.5vw; } } }
  &__text {
    width: 225px;
    height: 74px;
    display: inline-block;
    font-family: $museo;
    font-size: 20px;
    color: $white;
    align-self: flex-end;
    @include respond-to(543) {
      align-self: center;
      margin-top: 20px; }
    @include respond-to(480) {
      width: 41vw;
      height: auto;
      font-size: 3.8vw; } } }
