.features {
  margin-top: 100px;
  &__wrapper {}
  &__item {
    width: 225px;
    margin-bottom: 38px;
    @include respond-to(480) {
      width: 42vw; } }
  &__img {
    display: inline-block;
    width: 60px;
    height: 60px;
    border-radius: 5px;
    overflow: hidden;
    border: 1px solid $grey2;
    @include respond-to(480) {
      position: relative; }
    img {
      width: 100%;
      object-fit: cover; } }
  &__text {
    font-family: $museo;
    font-size: 16px;
    color: $grey;
    margin-top: 20px;
    @include respond-to(480) {
      font-size: 3.5vw; } } }
